<template>
  <view class="main-box">
    <!-- #ifdef H5 -->
    <view v-for="(item, index) in componentsConfig" :key="index">
      <component :is="item.name" :dataConfig="item" />
    </view>
    <!-- #endif -->

    <!-- #ifdef MP || APP-PLUS -->
    <block v-for="(item, index) in componentsConfig" :key="index">
      <swipers v-if="item.name == 'swipers'" :dataConfig="item" />
      <menus v-if="item.name == 'menus'" :dataConfig="item" />
      <noticeBar v-if="item.name == 'noticeBar'" :dataConfig="item" />
      <cardBox v-if="item.name == 'cardBox'" :dataConfig="item" />
      <activityList v-if="item.name == 'activityList'" :dataConfig="item" />
    </block>
    <!-- #endif -->
  </view>
</template>

<script>
import shareMixins from '@/mixins/shareMixins'
import swipers from './components/swipers/index.vue'
import ActivityItem from '@/components/ActivityItem/index.vue'
import cardBox from './components/cardBox/index.vue'
import activityList from './components/activityList/index.vue'
import noticeBar from './components/noticeBar/index.vue'
import menus from './components/menus/index.vue'
import { config as wzConfig } from './pageSet/wzConfig'
import { config as rcConfig } from './pageSet/rcConfig'
export default {
  mixins: [shareMixins],
  components: {
    ActivityItem,
    swipers,
    activityList,
    cardBox,
    noticeBar,
    menus,
  },
  data() {
    return {
      // 临时配置
      config: {},
      componentsConfig: [],
    }
  },
  onLoad(options) {
    this.config = this.$store.app.themeType == '1' ? rcConfig : wzConfig
    this.componentsConfig = this.objToArr(this.config)
  },
  methods: {
    objToArr(data) {
      let obj = Object.keys(data)
      let m = obj.map((key) => data[key])
      return m
    },
  },
}
</script>

<style scoped lang="scss">
.main-box {
  background-color: #fff;
  padding-bottom: 240rpx;
}
</style>
